
.channel-title-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    border-bottom: solid 2px;
    margin-bottom: 2%;
}

.channel-title-container h3 {
    font-size: 1.5rem;
    font-weight: 400;
    letter-spacing: var(--font-spacing);
}