.video-container {
    width: 100%;
    height: 100%;
    position: relative;
    min-height: 300px;
    min-width: 300px;
    max-height: 700px;
}

.video-container video {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.video-container .video-controls-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    width: 120px;
    height: 120px;
    justify-content: center;
    align-items: center;
}

