
.active-user-container {
    width: 100%;
    height: 100%;
    max-width: 500px;
    position: relative;
    max-height: 300px;
    border-radius: var(--border-radius);
    overflow: hidden;
    flex-shrink: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.active-user-container video {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.active-user-profile-image-container {
    width: 150px;
    height: 150px;
    position: relative;
    border-radius: 50%;
    overflow: hidden;
}