
.join-server-container {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    align-items: center;
    position: relative;
}

.join-server-inner-container {
    width: 95%;
    display: flex;
    flex-direction: column;
}

.join-server-banner-container {
    height: 500px;
    border-radius: var(--border-radius);
    overflow: hidden;
    margin-top: 2.5%;
    position: relative;
}
.join-server-banner-container h1 {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    margin: 0;
    padding: 1rem 0;
}