
#live-chat-wrapper {
    min-width: 100%;
    height: calc(100% - 55px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    position: relative;
    overflow: hidden;
}

#live-chat-wrapper .streaming-video-player {
    max-width: 500px;
    border-radius: var(--border-radius);
    max-height: 300px;
}

